<template>
    <div class='warp'>
        <div class='top'>
           <p @click='back'>全部颜色</p>
        </div>
        <section>
            <nav> 
                <p v-for='(item,index) in colorList' :key='index' @click='changYear(index)'>
                        {{index}}       
                </p>
            </nav>
            <ul class='list'>
                <li v-for='(item,index) in lists' :key='index'>
                    <span :style="{background:item.Value}"></span>
                    {{item.Name}} 
                </li>
            </ul>
        </section>
    </div>
</template>
<script>
    import {
        mapState,
        mapActions,
        mapMutations
    } from 'vuex'
    export default {
        computed: {
            ...mapState({
                colorList: state => state.color.colorList,
                lists: state => state.color.lists
            }),
        },
        methods: {
            ...mapActions({
                getColorLists: 'color/getColorLists'
            }),
            ...mapMutations({
                changYear: 'color/changYear'
            }),
            back() {
                this.$router.back()
            }
        },
        mounted() {
            console.log(this.$route.query.id);
            this.getColorLists(this.$route.query.id)
        }
    }
</script>
<style scoped lang='scss'>
    .warp {
        width: 100%;
        height: 100%;
        background: #eee;
        display: flex;
        flex-direction: column;
    }
    
    .top {
        background: #fff;
        width: 100%;
        height: .8rem;
        -webkit-box-align: center;
        align-items: center;
        box-sizing: border-box;
        z-index: 99;
        display: flex;
        justify-content: space-around;
        p {
            font-size: .28rem;
            color: #00afff
        }
    }
    
    section {
        margin-top: .2rem;
        width: 100%;
        height: 100%;
        overflow: scroll;
        flex: 1;
        nav {
            display: flex;
            margin-top: .15rem;
            padding-left: .2rem;
            font-size: .3rem;
            line-height: .76rem;
            height: .76rem;
            background: #fff;
            overflow-x: scroll;
            p {
                padding-right: .42rem
            }
        }
        .list {
            margin-top: .15rem;
            padding: 0 .2rem;
            overflow: hidden;
            background: #fff;
            li {
                float: left;
                width: 3.45rem;
                font-size: .32rem;
                line-height: .68rem;
                border: 1px solid #3aacff;
                box-sizing: border-box;
                margin: .2rem 0;
                border-radius: .05rem;
                span {
                    display: inline-block;
                    width: .4rem;
                    height: .4rem;
                    margin-left: .2rem;
                    vertical-align: -12%;
                    background: #fe9210;
                    box-sizing: border-box;
                    border: 1px solid #818181;
                }
            }
        }
    }
</style>


